    formateDataToVisualize(legalFiles) {
        // this.log("formateDataToVisualize - this.legalFiles", this.legalFiles);
        let legalFilesDay = [];
        this.creationDates = [];
        for (let legalFile of legalFiles) {
            this.creationDates = [
                ...this.creationDates,
                {
                    // legalFile.creationDate
                    day: new Date(legalFile.file.date).toISOString().split("T")[0],
                    legalFiles: [],
                    isCreationDate: true,
                    withPeriods: (legalFile.periods && legalFile.periods.length) ? true : false
                }
            ];
            /*
                this.creationDates.push({
                  day: new Date(legalFile.creationDate)
                    .toISOString()
                    .split("T")[0],
                  legalFiles: [],
                  isCreationDate: true
                })
                */
        }
        for (let legalFile of legalFiles) {
            let daysArray = [];
            if (legalFile.periods && legalFile.periods.length) {
                daysArray = this.convertPeriodsToDaysArray(legalFile.periods);
            }
            for (let day of daysArray) {
                let findDayIndex = legalFilesDay.findIndex(d => d.day === day);
                // this.log("findDayIndex", findDayIndex);
                if (findDayIndex !== -1) {
                    // this.log("i go here - findDayIndex", findDayIndex);
                    legalFilesDay[findDayIndex].legalFiles = [
                        ...legalFilesDay[findDayIndex].legalFiles,
                        legalFile
                    ];
                    // legalFilesDay[findDayIndex].legalFiles.push(legalFile);
                }
                else {
                    /*
                            legalFilesDay.push({
                              day,
                              legalFiles: [legalFile]
                            });
                            */
                    legalFilesDay = [
                        ...legalFilesDay,
                        {
                            day,
                            legalFiles: [legalFile]
                        }
                    ];
                }
            }
        }
        /*
                data like :
                data: [
                    {
                        day: 2016-05-12,
                        legalFiles: [...]
                    }
                ]
            */
        return legalFilesDay;
    }
    convertPeriodsToDaysArray(periods) {
        let daysArray = [];
        // retourne les jours compris entre start et end
        for (let period of periods) {
            // this.log("convertPeriodsToDaysArray - period", period);
            let min = new Date(period.start);
            let max = new Date(period.end);
            max.setDate(max.getDate() + 1);
            /*
            this.log(
              "convertPeriodsToDaysArray - min.toISOString()",
              min.toISOString()
            );
            */
            /*
            this.log(
              "convertPeriodsToDaysArray - max.toISOString()",
              max.toISOString()
            );
            */
            min.setHours(8, 0, 0, 0);
            max.setHours(8, 0, 0, 0);
            while (new Date(min) < new Date(max)) {
                // keep only 'yyyy-mm-dd'
                const dateFormated = min.toISOString().split("T")[0];
                // const dateFormated = this.formatDate(new Date(min));
                if (!daysArray.some(d => d === dateFormated)) {
                    daysArray = [...daysArray, dateFormated];
                    // daysArray.push(dateFormated)
                }
                let inc = new Date(min);
                inc.setDate(min.getDate() + 1);
                min = inc;
                /*
                this.log(
                  "convertPeriodsToDaysArray - inc.toISOString()",
                  min.toISOString()
                );
                */
            }
            /*
            this.log("convertPeriodsToDaysArray - return period", {
              start: new Date(period.start).toISOString().split("T")[0],
              end: new Date(period.end).toISOString().split("T")[0]
            });
            */
        }
        // this.log("convertPeriodsToDaysArray - return daysArray", daysArray);
        return daysArray;
    }
