s3p-legal-files #legalFilesChart {
  margin: 10px 20px;
}
s3p-legal-files .month {
  margin-right: 8px;
}
s3p-legal-files .month-name {
  font-size: 60%;
  fill: #777;
  font-family: Arial, Helvetica;
}
s3p-legal-files .day.hover {
  stroke: #6d6e70;
  stroke-width: 2;
}
s3p-legal-files .day.focus {
  stroke: #ffff33;
  stroke-width: 2;
}
s3p-legal-files .cross.hover {
  stroke: #6d6e70;
  stroke-width: 2;
}
s3p-legal-files .cross.focus {
  stroke: #ffff33;
  stroke-width: 2;
}
